.img-container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  font-family: "Parisienne", cursive;
}
.img {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background-size: cover;
  background-position: 50% 50%;
}

.img-cover {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
}

.img-cover:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.3);
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(232, 232, 232, 0.2)),
    to(rgba(0, 0, 0, 0.62))
  );
  background: -webkit-linear-gradient(
    top,
    rgba(232, 232, 232, 0.2),
    rgba(0, 0, 0, 0.62)
  );
  background: -o-linear-gradient(
    top,
    rgba(232, 232, 232, 0.2),
    rgba(0, 0, 0, 0.62)
  );
  background: linear-gradient(
    to bottom,
    rgba(232, 232, 232, 0.2),
    rgba(0, 0, 0, 0.62)
  );
}



.name-container {
  position: absolute;
  width: 100%;
  height: auto;
  float: left;
  bottom: 190px;
  left: 50%;
  -webkit-transform: translate(-50%,-51%);
      -ms-transform: translate(-50%,-51%);
          transform: translate(-50%,-51%);
  text-align: center;
}

.name-container span {
  display: inline-block;
}

.name-container span:nth-child(2n){
  margin: 0 10px 0 0;
}

.name-container.date-time {
  bottom:130px;
  text-align:center;
  
}

.name-container.date-time .name {
  font-size: 2.2rem;
  font-family: 'Italianno', cursive;  
}

.name-container.location {
  bottom: 90px;
  text-align: center;
}

.name-container.location .name{
  font-size: 2rem;
  font-family: 'Italianno', cursive;

}


