.App {
  position: absolute;
  height: 100vh;
  width:100vw;
  overflow: hidden;
  overflow-y:scroll;
}

*{
  padding:0;
  margin:0;
  outline: none;
}

.home-page{
  width: 100vw;
  height: 100vh;
  position: relative;
}

.home-page .icon{
  position: fixed;
  bottom: 0;
  right: 0;
}
.home-page .icon .SVGInline-svg path{
  fill:#FFD700;
}

.swiper-container{
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}


ul {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

li.layer {
  width: 100vw;
  height: auto;
}



