.layer{
    position: absolute;
    width: 100vw;
    height: 100vh;
}

.name{
    font-family: 'Italianno', cursive;
    color: #FFD700;
    font-weight: bold;
    font-size: 3rem;
    display: inline-block;
}

.bride.name {

}

.groom.name {
}

span.weds-text {
    color: #FFD700;
    font-weight: bold;
    font-size: 3rem;
    display: inline-block;
    font-family: 'Italianno', cursive;
  }

  .name-container.quotes {
    bottom: calc(3rem + 219px);
    color: #FFD700;
    font-family: 'Italianno', cursive;
    font-size: 2rem;
    font-weight: bold;
}

.name-container.location .icon {
    position: relative;
    top: -5px;
}

.name-container.location  .icon path {
    fill: #FFD700;
    font-size: 7rem;
}

.name-container.location  .icon svg.SVGInline-svg {
    -webkit-transform:scale(1.3);
        -ms-transform:scale(1.3);
            transform:scale(1.3);
    margin-right:10px;
}


